import React, { useEffect, useState } from 'react';
import { View, Text, StyleSheet, TouchableOpacity, ScrollView } from 'react-native';
import Icon from 'react-native-vector-icons/Ionicons';
import { styles } from '../../css/homedashboardstyle';
import * as Battery from 'expo-battery';

const HomeDashboard = () => {
  const [appSessionTime, setAppSessionTime] = useState(0); // Track time spent in the app
  const [batteryLevel, setBatteryLevel] = useState('Loading...');
  
  useEffect(() => {
    // Start timer to track session duration
    const startTime = Date.now();
    const intervalId = setInterval(() => {
      setAppSessionTime(Math.floor((Date.now() - startTime) / 1000)); // Track session in seconds
    }, 1000);

    // Fetch battery level using expo-battery
    const fetchBatteryLevel = async () => {
      const level = await Battery.getBatteryLevelAsync();
      setBatteryLevel(`${Math.floor(level * 100)}%`); // Convert to percentage
    };

    fetchBatteryLevel();

    // Cleanup the interval on component unmount
    return () => clearInterval(intervalId);
  }, []);

  return (
    <ScrollView contentContainerStyle={styles.contentContainer}>
      <View style={styles.dashboardContainer}>
        {/* Device Usage Section */}
        <View style={styles.usageContainer}>
          <Text style={styles.sectionHeader}>Device Usage</Text>
          <View style={styles.usageDetails}>
            <View style={styles.usageItem}>
              <Icon name="timer-outline" size={24} color="#333" />
              <Text style={styles.usageLabel}>App Session Time:</Text>
              <Text style={styles.usageValue}>{Math.floor(appSessionTime / 60)}m {appSessionTime % 60}s</Text>
            </View>
            <View style={styles.usageItem}>
              <Icon name="battery-half-outline" size={24} color="#333" />
              <Text style={styles.usageLabel}>Battery Level:</Text>
              <Text style={styles.usageValue}>{batteryLevel}</Text>
            </View>
          </View>
        </View>

        {/* App Schedules Section */}
        <View style={styles.schedulesContainer}>
          <Text style={styles.sectionHeader}>App Schedules</Text>
          <TouchableOpacity style={styles.scheduleCard}>
            <Icon name="logo-youtube" size={24} color="#FF0000" />
            <Text style={styles.scheduleTitle}>YouTube</Text>
            <Text style={styles.scheduleTime}>Allowed: 1h/day</Text>
          </TouchableOpacity>
          <TouchableOpacity style={styles.scheduleCard}>
            <Icon name="logo-tiktok" size={24} color="#000" />
            <Text style={styles.scheduleTitle}>TikTok</Text>
            <Text style={styles.scheduleTime}>Blocked: 8pm - 7am</Text>
          </TouchableOpacity>
          <TouchableOpacity style={styles.scheduleCard}>
            <Icon name="game-controller-outline" size={24} color="#333" />
            <Text style={styles.scheduleTitle}>Gaming</Text>
            <Text style={styles.scheduleTime}>Allowed: Weekends Only</Text>
          </TouchableOpacity>
        </View>
      </View>
    </ScrollView>
  );
};

export default HomeDashboard;
