import { getAuth, GoogleAuthProvider, FacebookAuthProvider, signInWithCredential, fetchSignInMethodsForEmail } from "firebase/auth";
import { showMessage } from 'react-native-flash-message';
import { saveUserRole, getUserRole } from './firestoreService'; 
import { auth } from '../firebase'; 
import AsyncStorage from '@react-native-async-storage/async-storage';
// Handle Google Sign-In
export const handleGoogleSignIn = async (
    accessToken: string,
    navigation: any,
    selectedRole: string,
    setLoading: (loading: boolean) => void
  ) => {
    const auth = getAuth();
    setLoading(true); // Start loading
    try {
      const credential = GoogleAuthProvider.credential(null, accessToken);
      const userCredential = await signInWithCredential(auth, credential);
      const userId = userCredential.user.uid;
  
      // Fetch the user's role from Firestore or your DB
      let userRole = await getUserRole(userId);
      console.log('Fetched user role:', userRole); // Log the role for debugging
  
      // Handle if no role is found (first-time sign-in)
      if (!userRole) {
        console.log('No role found for the user, assigning selected role...');
        userRole = selectedRole; // Assign the selected role
        await saveUserRole(userId, userRole); // Save role in Firestore or your DB
        console.log('Assigned and saved role:', userRole);
      }
  
      // Check if selected role matches the user's role in DB
      if (userRole !== selectedRole) {
        showMessage({
          message: 'Role Mismatch',
          description: `You selected ${selectedRole}, but your account role is ${userRole}.`,
          type: 'danger',
          icon: 'danger',
          duration: 8000,
        });
        return; // Exit if role mismatch
      }
  
      // Show success message and navigate to the correct layout based on role
      showMessage({
        message: 'Login Successful',
        description: `Welcome ${userCredential.user.email}`,
        type: 'success',
        icon: 'success',
        duration: 8000,
      });
  
      // Navigate to respective layout based on role
      navigation.navigate(userRole === 'parent' ? '_parentLayout' : '_childLayout');
    } catch (error: any) {
      console.log('Google Sign-In Error:', error); // Log the error
      if (error.code === 'auth/account-exists-with-different-credential') {
        const email = error.customData.email;
        const methods = await fetchSignInMethodsForEmail(auth, email);
        showMessage({
          message: 'Google Sign-In Error',
          description: `This email is already associated with a ${methods[0]} account.`,
          type: 'danger',
          icon: 'danger',
          duration: 8000,
        });
      } else {
        showMessage({
          message: 'Google Sign-In Error',
          description: error.message,
          type: 'danger',
          icon: 'danger',
          duration: 8000,
        });
      }
    } finally {
      setLoading(false); // Stop loading after process completes
    }
  };
  
  // Handle Facebook Sign-In
export const handleFacebookSignIn = async (
  accessToken: string,
  navigation: any,
  selectedRole: string,
  setLoading: (loading: boolean) => void
) => {
  const auth = getAuth();
  setLoading(true); // Start loading
  try {
    const credential = FacebookAuthProvider.credential(accessToken);
    const userCredential = await signInWithCredential(auth, credential);
    const userId = userCredential.user.uid;

    // Fetch the user's role from Firestore or your DB
    let userRole = await getUserRole(userId);
    console.log('Fetched user role:', userRole); // Log the role for debugging

    // Handle if no role is found (first-time sign-in)
    if (!userRole) {
      console.log('No role found for the user, assigning selected role...');
      userRole = selectedRole; // Assign the selected role
      await saveUserRole(userId, userRole); // Save role in Firestore or your DB
      console.log('Assigned and saved role:', userRole);
    }

    // Check if selected role matches the user's role in DB
    if (userRole !== selectedRole) {
      showMessage({
        message: 'Role Mismatch',
        description: `You selected ${selectedRole}, but your account role is ${userRole}.`,
        type: 'danger',
        icon: 'danger',
        duration: 8000,
      });
      return; // Exit if role mismatch
    }

    // Show success message and navigate to the correct layout based on role
    showMessage({
      message: 'Login Successful',
      description: `Welcome ${userCredential.user.email}`,
      type: 'success',
      icon: 'success',
      duration: 8000,
    });

    // Navigate to respective layout based on role
    navigation.navigate(userRole === 'parent' ? '_parentLayout' : '_childLayout');
  } catch (error: any) {
    console.log('Facebook Sign-In Error:', error); // Log the error
    if (error.code === 'auth/account-exists-with-different-credential') {
      const email = error.customData.email;
      const methods = await fetchSignInMethodsForEmail(auth, email);
      showMessage({
        message: 'Facebook Sign-In Error',
        description: `This email is already associated with a ${methods[0]} account.`,
        type: 'danger',
        icon: 'danger',
        duration: 8000,
      });
    } else {
      showMessage({
        message: 'Facebook Sign-In Error',
        description: error.message,
        type: 'danger',
        icon: 'danger',
        duration: 8000,
      });
    }
  } finally {
    setLoading(false); // Stop loading after process completes
  }
};

//......................................................................................................................................
export const handleProviderSignIn = async (
  credential, 
  navigation, 
  selectedRole, 
  setLoading, 
  providerName
) => {
  try {
    const userCredential = await signInWithCredential(auth, credential);
    const userId = userCredential.user.uid;
    let userRole = await getUserRole(userId);

    if (!userRole) {
      userRole = selectedRole;
      await saveUserRole(userId, userRole);
    }

    if (userRole !== selectedRole) {
      showMessage({
        message: `${providerName} Sign-In Error`,
        description: `You selected ${selectedRole}, but your account role is ${userRole}.`,
        type: 'danger',
      });
      return;
    }

    await AsyncStorage.setItem('userToken', userId);
    await AsyncStorage.setItem('userRole', userRole);

    showMessage({
      message: `${providerName} Login Successful`,
      description: `Welcome ${userCredential.user.email}`,
      type: 'success',
    });

    navigation.replace(userRole === 'parent' ? '_parentLayout' : '_childLayout');
  } catch (error) {
    showMessage({
      message: `${providerName} Sign-In Error`,
      description: error.message || 'An error occurred during sign-in.',
      type: 'danger',
    });
  } finally {
    setLoading(false);
  }
};