import React, { useState } from 'react';
import { View, Text, StyleSheet, ScrollView, Dimensions, TouchableOpacity, Modal } from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { Ionicons, MaterialIcons, FontAwesome, Entypo } from '@expo/vector-icons'; // Add more icons
import SetSchedule from './Schedule'; // Your SetSchedule component

const { width, height } = Dimensions.get('window');

const Dashboard: React.FC = () => {
  const [modalVisible, setModalVisible] = useState(false);

  const toggleModal = () => {
    setModalVisible(!modalVisible); // To show/hide the schedule modal
  };

  const navigateToMonitorScreen = () => {
    // Placeholder for navigation to Monitor Screen
    console.log("Navigating to Monitor Screen");
  };

  return (
    <View style={styles.container}>
      <ScrollView contentContainerStyle={styles.contentContainer}>
        {/* Total Spend Time Summary */}
        <View style={styles.summary}>
          <Ionicons name="time-outline" size={40} color="#388E3C" style={styles.iconStyle} />
          <View style={styles.summaryTextContainer}>
            <Text style={styles.summaryText}>Total Time Spent on Device Today</Text>
            <Text style={styles.time}>2h 35m</Text>
          </View>
        </View>

        <View style={styles.divider} />

        {/* Control Buttons */}
        <View style={styles.controlsContainer}>
          {/* Set Schedule Button */}
          <TouchableOpacity onPress={toggleModal} style={styles.controlButtonWrapper}>
            <LinearGradient
              colors={['#4CAF50', '#388E3C']}
              start={[0, 0]}
              end={[1, 1]}
              style={styles.controlButton}
            >
              <Ionicons name="calendar-outline" size={24} color="#fff" style={styles.buttonIcon} />
              <Text style={styles.controlButtonText}>Set Schedule</Text>
            </LinearGradient>
          </TouchableOpacity>

          {/* Monitor Screen Button */}
          <TouchableOpacity onPress={navigateToMonitorScreen} style={styles.controlButtonWrapper}>
            <LinearGradient
              colors={['#2196F3', '#1976D2']}
              start={[0, 0]}
              end={[1, 1]}
              style={styles.controlButton}
            >
              <Ionicons name="eye-outline" size={24} color="#fff" style={styles.buttonIcon} />
              <Text style={styles.controlButtonText}>Monitor Screen</Text>
            </LinearGradient>
          </TouchableOpacity>

          {/* View Application Usage Button */}
          <TouchableOpacity style={styles.controlButtonWrapper}>
            <LinearGradient
              colors={['#FF9800', '#F57C00']}
              start={[0, 0]}
              end={[1, 1]}
              style={styles.controlButton}
            >
              <MaterialIcons name="apps" size={24} color="#fff" style={styles.buttonIcon} />
              <Text style={styles.controlButtonText}>View Applications Usage</Text>
            </LinearGradient>
          </TouchableOpacity>
        </View>

        <View style={styles.divider} />

        {/* App Usage Data */}
        {data.map((item, index) => (
          <View key={index} style={styles.appUsage}>
            <View style={styles.appInfo}>
              <FontAwesome size={24} color="#333" />
              <Text style={styles.appLabel}>{item.label}</Text>
            </View>
            <Text style={styles.appTime}>{item.time}</Text>
          </View>
        ))}

        <View style={styles.divider} />
      </ScrollView>

      {/* Modal for Set Schedule */}
      <Modal visible={modalVisible} animationType="slide" transparent={true} onRequestClose={toggleModal}>
        <View style={styles.modalContainer}>
          <View style={styles.modalContent}>
            <SetSchedule />
          </View>
        </View>
      </Modal>
    </View>
  );
};

// Sample Data for App Usage with Icons
const data = [
  { icon: 'facebook', label: 'Facebook', time: '1h 10m' },
  { icon: 'youtube', label: 'YouTube', time: '50m' },
  { icon: 'twitter', label: 'Twitter', time: '30m' },
  { icon: 'instagram', label: 'Instagram', time: '45m' },
];

// Styles
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#f0f0f0', // Lighter background for better contrast
  },
  contentContainer: {
    paddingVertical: 20,
    alignItems: 'center',
  },
  summary: {
    flexDirection: 'row', // Align items horizontally
    alignItems: 'center',
    marginVertical: 10,
    backgroundColor: '#ffffff',
    padding: 20,
    borderRadius: 20,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 4 },
    shadowOpacity: 0.1,
    shadowRadius: 3,
    elevation: 5,
    width: width * 0.9,
  },
  summaryTextContainer: {
    flex: 1,
    marginLeft: 15,
  },
  summaryText: {
    fontSize: 18,
    color: '#333',
    fontWeight: '600',
  },
  time: {
    fontSize: 36,
    fontWeight: 'bold',
    color: '#388E3C',
  },
  divider: {
    height: 1,
    backgroundColor: '#ddd',
    marginVertical: 20,
    width: width * 0.8,
  },
  controlsContainer: {
    marginTop: 20,
    width: width * 0.9,
  },
  controlButtonWrapper: {
    marginBottom: 15,
  },
  controlButton: {
    flexDirection: 'row',
    alignItems: 'center',
    paddingVertical: 12,
    paddingHorizontal: 15,
    borderRadius: 8,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 4 },
    shadowOpacity: 0.3,
    shadowRadius: 5,
    elevation: 5,
  },
  buttonIcon: {
    marginRight: 10,
  },
  controlButtonText: {
    color: '#fff',
    fontWeight: 'bold',
    fontSize: 16,
    textAlign: 'center',
  },
  appUsage: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginVertical: 10,
    backgroundColor: '#ffffff',
    padding: 15,
    borderRadius: 15,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.1,
    shadowRadius: 3,
    elevation: 5,
    width: width * 0.9,
  },
  appInfo: {
    flexDirection: 'row',
    alignItems: 'center',
  },
  appLabel: {
    fontSize: 16,
    color: '#333',
    marginLeft: 10,
  },
  appTime: {
    fontSize: 16,
    color: '#333',
  },
  modalContainer: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  modalContent: {
    width: width * 1, // Adjust width as needed
    height: height * 1, // Adjust height to 70% of the screen
    borderRadius: 10,
    padding: 20,
    position: 'relative', // Required for positioning the close button
  },
  iconStyle: {
    marginRight: 10,
  },
});

export default Dashboard;
