import React, { useState, useEffect } from 'react';
import { View, TouchableOpacity, StyleSheet, Animated, Dimensions, Platform } from 'react-native';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { Ionicons } from '@expo/vector-icons';
import CustomTabBar from './components/childCustombar';
import Home from './(Childscreens)';
import Location from './(Childscreens)/cLocation';
import ChildSidebar from './components/ChildSidebar';

const { width } = Dimensions.get('window');
const Tab = createBottomTabNavigator();

const _ChildLayout: React.FC<{ deviceId: string }> = ({ deviceId }) => {
  const [sidebarVisible, setSidebarVisible] = useState(false);
  const sidebarPosition = useState(new Animated.Value(-width * 0.75))[0];

  const translateX = useState(new Animated.Value(-width * 0.75))[0];

  useEffect(() => {
    Animated.timing(sidebarPosition, {
      toValue: sidebarVisible ? 0 : -width * 0.75,
      duration: 300,
      useNativeDriver: false,
    }).start();
  }, [sidebarVisible]);

  
  const toggleSidebar = () => {
    setSidebarVisible(!sidebarVisible);
  };

  return (
    <View style={styles.container}>
      <Tab.Navigator tabBar={(props) => <CustomTabBar {...props} />}>
        <Tab.Screen name="Home" component={Home} options={{ headerShown: false }} />
        <Tab.Screen name="Location" component={Location} options={{ headerShown: false }} />
      </Tab.Navigator>
      <TouchableOpacity onPress={toggleSidebar} style={styles.profileButton}>
        <Ionicons name="reorder-three-outline" size={40} color="#8e8d8d" />
      </TouchableOpacity>
      <ChildSidebar
        sidebarPosition={sidebarPosition}
        toggleSidebar={toggleSidebar}
        deviceId={deviceId}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#f8f9fa',
  },
  profileButton: {
    position: 'absolute',
    top: 6,
    right: 20,
    width: 50,
    height: 50,
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 20,
  },
});

export default _ChildLayout;
