import React, { useState, useEffect } from 'react';
import { View, TouchableOpacity, StyleSheet, Animated, Dimensions } from 'react-native';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { Ionicons } from '@expo/vector-icons';
import CustomTabBar from './components/parentcustomtabber'; 
import Child from './(ParentScreens)/child';
import PHome from './(ParentScreens)/index';
import Notifications from './(ParentScreens)/notifications';
import ParentSidebar from './components/ParentSidebar';
const { width } = Dimensions.get('window');
const Tab = createBottomTabNavigator();


const _parentLayout: React.FC = () => {
  const [sidebarVisible, setSidebarVisible] = useState(false);
  const sidebarPosition = useState(new Animated.Value(-width * 0.75))[0];
  useEffect(() => {
    Animated.timing(sidebarPosition, {
      toValue: sidebarVisible ? 0 : -width * 0.75,
      duration: 300,
      useNativeDriver: false,
    }).start();
  }, [sidebarVisible]);
  const toggleSidebar = () => {
    setSidebarVisible(!sidebarVisible);
  };
  return (
    <View style={styles.container}>
      <Tab.Navigator tabBar={(props) => <CustomTabBar {...props} />}>
        <Tab.Screen name="Home" component={PHome} options={{ headerShown: false }} />
        <Tab.Screen name="Child" component={Child} options={{ headerShown: false }} />
        <Tab.Screen name="Notifications" component={Notifications} options={{ headerShown: false }} />
      </Tab.Navigator>
      <TouchableOpacity onPress={toggleSidebar} style={styles.profileButton}>
        <Ionicons name="reorder-three-outline" size={40} color="#8e8d8d" />
      </TouchableOpacity>
      <ParentSidebar sidebarPosition={sidebarPosition} toggleSidebar={toggleSidebar} />
    </View>
  );
};
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#f8f9fa',
  },
  profileButton: {
    position: 'absolute',
    top: 6,
    right: 20,
    width: 50,
    height: 50,
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 20,
  },
});
export default _parentLayout;