import React from 'react';
import { View, Text, Image, StyleSheet, FlatList, TouchableOpacity, Dimensions } from 'react-native';
import { useNavigation, NavigationProp } from '@react-navigation/native';
import { RootStackParamList } from '../types/navigation';

const children = [
  { name: 'Example 1', location: 'Example Location 1', active: true },
  { name: 'Example 2', location: 'Example Location 2', active: false },
];

const { width } = Dimensions.get('window');

const _ChildList: React.FC = () => {
  const navigation = useNavigation<NavigationProp<RootStackParamList>>();

  const manageItem = (item) => {
    console.log('Manage item:', item);
    navigation.navigate('ManageChild', { item });
  };

  const deleteItem = (item) => {
    console.log('Delete item:', item);
  };

  const renderItem = ({ item }) => (
<View style={styles.itemContainer}>
  <Image
    resizeMode="contain"
    source={{
      uri: "https://cdn.builder.io/api/v1/image/assets/TEMP/435f2fe62be7b790756d95367b4366c5157f52f9e42bfa609a3893cb2ea45ec8?apiKey=946fd47e70b14e83a4cc54f9fc69027c",
    }}
    style={styles.childImage}
  />
  <View style={styles.divider} />
  <View>
    <Text style={styles.childName}>{item.name}</Text>
    <Text style={styles.childLocation}>{item.location}</Text>
    <Text style={[styles.childStatus, { color: item.active ? 'green' : 'red' }]}>
      {item.active ? 'Active' : 'Inactive'}
    </Text>
  </View>
  <View style={styles.buttonsContainer}>
    <TouchableOpacity style={styles.actionButton} onPress={() => manageItem(item)}>
      <Text style={styles.actionText}>Manage</Text>
    </TouchableOpacity>
    <TouchableOpacity style={styles.actionButton} onPress={() => deleteItem(item)}>
      <Text style={styles.actionText}>Delete</Text>
    </TouchableOpacity>
  </View>
  
</View>

  );

  return (
    <View style={styles.container}>
      <FlatList
        data={children}
        renderItem={renderItem}
        keyExtractor={(item, index) => index.toString()}
        contentContainerStyle={styles.childList}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    padding: 10,
    borderRadius: 10,
    width: width * 0.94,
  },
  childList: {
    alignItems: 'flex-start',
  },
  itemContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    marginBottom: 10,
    backgroundColor: '#fff',
    padding: 10,
    borderRadius: 12,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.1,
    shadowRadius: 5,
    elevation: 3,
  },
  childImage: {
    width: 50,
    height: 50,
    borderRadius: 25,
    marginRight: 10,
  },
  childDetailsContainer: {
    flex: 2,
    justifyContent: 'center',
    paddingHorizontal: 10, // Added padding for better spacing
  },
  childName: {
    fontSize: 16,
    fontWeight: '600',
    marginBottom: 2,
    color: '#000',
  },
  childLocation: {
    fontSize: 14,
    color: '#555',
    marginBottom: 2,
  },
  childStatus: {
    fontSize: 14,
    fontWeight: '500',
  },
  divider: {
    width: 1,
    height: '100%',
    backgroundColor: '#ddd',
    marginHorizontal: 10,
  },
  buttonsContainer: {
    flexDirection: 'row',
    alignItems: 'center',
  },
  actionButton: {
    paddingVertical: 8,
    paddingHorizontal: 12,
    borderRadius: 10,
    backgroundColor: '#4CAF50',
    marginLeft: 5,
  },
  actionText: {
    color: 'white',
    fontWeight: '600',
  },
});
export default _ChildList;
