import React, { useEffect, useState, useRef } from 'react';
import { View, Text, StyleSheet, Image, Dimensions, Animated, ActivityIndicator } from 'react-native';
import { fetchUserData } from '../services/userDataService';
import { getAuth } from 'firebase/auth';

const { height } = Dimensions.get('window');

// Bubble Component
const Bubble: React.FC<{ style: any }> = ({ style }) => {
  return <Animated.View style={[styles.bubble, style]} />;
};

const Header: React.FC = () => {
  const [parentName, setParentName] = useState({ firstName: '', lastName: '' });
  const [loading, setLoading] = useState(true); // Add loading state
  const bubbleAnimation1 = useRef(new Animated.Value(0)).current;
  const bubbleAnimation2 = useRef(new Animated.Value(0)).current;

  useEffect(() => {
    const fetchData = async () => {
      try {
        const auth = getAuth();
        const currentUser = auth.currentUser;

        if (currentUser) {
          const userData = await fetchUserData(currentUser.uid);
          setParentName(userData);
        }
      } catch (error) {
        console.error('Error fetching user data:', error);
      } finally {
        setLoading(false); // Stop loading once fetch is done
      }
    };

    fetchData();
  }, []);

  useEffect(() => {
    // Bubble 1 Animation
    Animated.loop(
      Animated.sequence([
        Animated.timing(bubbleAnimation1, {
          toValue: 1,
          duration: 6000,
          useNativeDriver: true,
        }),
        Animated.timing(bubbleAnimation1, {
          toValue: 0,
          duration: 0,
          useNativeDriver: true,
        }),
      ])
    ).start();

    // Bubble 2 Animation
    Animated.loop(
      Animated.sequence([
        Animated.timing(bubbleAnimation2, {
          toValue: 1,
          duration: 5000,
          useNativeDriver: true,
        }),
        Animated.timing(bubbleAnimation2, {
          toValue: 0,
          duration: 0,
          useNativeDriver: true,
        }),
      ])
    ).start();
  }, []);

  // Interpolate position for bubble 1
  const translateY1 = bubbleAnimation1.interpolate({
    inputRange: [0, 1],
    outputRange: [height, -100], // Move from bottom to top
  });

  // Interpolate position for bubble 2
  const translateY2 = bubbleAnimation2.interpolate({
    inputRange: [0, 1],
    outputRange: [height, -150], // Move from bottom to top
  });

  if (loading) {
    return <ActivityIndicator size="large" color="#0000ff" style={{ flex: 1 }} />;
  }

  return (
    <View style={styles.headerContainer}>
      {/* Animated Bubbles */}
      <Bubble style={{ width: 100, height: 100, bottom: 0, left: -50, transform: [{ translateY: translateY1 }] }} />
      <Bubble style={{ width: 80, height: 80, bottom: -30, right: -40, transform: [{ translateY: translateY2 }] }} />

      {/* Text Container */}
      <View style={styles.textContainer}>
        <Text style={styles.greetingText}>
          Hi {parentName.firstName} {parentName.lastName}
        </Text>
        <Text style={styles.subText}>Welcome</Text>
      </View>

      {/* Profile Image */}
      <Image
        source={{ uri: 'https://via.placeholder.com/100' }} // Placeholder for profile image
        style={styles.profileImage}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  headerContainer: {
    position: 'relative',
    width: '100%',
    height: 190,
    paddingHorizontal: 20,
    paddingTop: 25,
    paddingBottom: 20,
    backgroundColor: '#fffecb',
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    overflow: 'hidden',
  },
  textContainer: {
    flex: 1,
  },
  greetingText: {
    fontSize: 28,
    fontWeight: 'bold',
    color: '#333',
  },
  subText: {
    fontSize: 16,
    color: '#666',
    marginTop: 5,
  },
  profileImage: {
    width: 80,
    height: 80,
    borderRadius: 50,
    borderWidth: 4,
    borderColor: '#fff',
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 4 },
    shadowOpacity: 0.3,
    shadowRadius: 5,
    elevation: 8,
  },
  bubble: {
    position: 'absolute',
    backgroundColor: 'rgba(135, 206, 250, 0.5)', // Light blue bubbles
    borderRadius: 50,
  },
});

export default Header;