import React, { useState, useEffect, useRef } from 'react';
import { View, StyleSheet, Text, ScrollView, TouchableOpacity, Modal, Dimensions, Platform } from 'react-native';
import { Ionicons, FontAwesome5 } from '@expo/vector-icons'; // Use Expo icons
import Scanner from '../components/Scanner'; // Import your Scanner component

const screenWidth = Dimensions.get('window').width;

interface ChildDashboardProps {
  ParentName: string;
  profileImages: string[];
  fontsLoaded: boolean;
  locations: string[];
  notifications: string[];
  schedules: string[];
}

const ChildDashboard: React.FC<ChildDashboardProps> = ({ profileImages, locations }) => {
  const [scannerVisible, setScannerVisible] = useState(false);
  const [selectedChild, setSelectedChild] = useState<number | null>(null); // State to track selected child
  const scrollViewRef = useRef<ScrollView>(null);
  const [activeIndex, setActiveIndex] = useState(0);

  const functionalities = [
    { name: 'Web Filtering', icon: 'globe', backgroundColor: '#f8b400', textColor: '#fff' },
    { name: 'Location of Child', icon: 'map-marker-alt', backgroundColor: '#007bff', textColor: '#fff' },
    { name: 'App Scheduling', icon: 'calendar-alt', backgroundColor: '#28a745', textColor: '#fff' },
    { name: 'Download Approval', icon: 'download', backgroundColor: '#dc3545', textColor: '#fff' },
  ];

  useEffect(() => {
    const interval = setInterval(() => {
      const nextIndex = (activeIndex + 1) % functionalities.length;
      scrollViewRef.current?.scrollTo({ x: nextIndex * screenWidth, animated: true });
      setActiveIndex(nextIndex);
    }, 2000); // 2 seconds interval

    return () => clearInterval(interval);
  }, [activeIndex]);

  const handleAddChild = () => {
    setScannerVisible(true); // Display scanner modal to add a new child
  };

  const closeScanner = () => {
    setScannerVisible(false);
  };

  return (
    <ScrollView contentContainerStyle={styles.contentContainer}>
      {/* Child Container with Add Child Button */}
      <View style={styles.childContainer}>
        <ScrollView
          horizontal
          showsHorizontalScrollIndicator={false}
          contentContainerStyle={styles.profileContainerContent}>
          {profileImages.map((image, index) => (
            <TouchableOpacity
              key={index}
              style={styles.profileImageWrapper}
              // Set selected child on click
              onPress={() => setSelectedChild(index)}
            >
              <Ionicons name="person-circle" size={60} color='black' style={styles.profileImage} />
              <Text style={styles.profileName}>{`Child ${index + 1}`}</Text>
            </TouchableOpacity>
          ))}
        </ScrollView>

        {/* Add Child Button */}
        <TouchableOpacity style={styles.addChildButton} onPress={handleAddChild}>
          <Ionicons name="add-circle" size={60} color="#4CAF50" />
          <Text style={styles.addChildText}>Add Child</Text>
        </TouchableOpacity>
      </View>
     
      {/* Swipeable Containers for Functionalities */}
      <View style={styles.swipeContainer}>
        <Text style={styles.greetingText}> Application Functionalities </Text>
        <ScrollView
          ref={scrollViewRef}
          horizontal
          pagingEnabled
          showsHorizontalScrollIndicator={false}>
          {functionalities.map((func, index) => (
            <View key={index} style={[styles.functionContainer, { backgroundColor: func.backgroundColor }]}>
              <FontAwesome5 name={func.icon} size={50} color={func.textColor} />
              <Text style={[styles.functionText, { color: func.textColor }]}>{func.name}</Text>
            </View>
          ))}
        </ScrollView>
      </View>

      {/* Modal for Scanner */}
      <Modal visible={scannerVisible} animationType="slide" transparent={true} onRequestClose={closeScanner}>
        <Scanner />
      </Modal>
    </ScrollView>
  );
};

const styles = StyleSheet.create({
  contentContainer: {
    flexGrow: 1,
    paddingBottom: 20,
  },
  content: {
    width: '100%',
    paddingHorizontal: 5,
    marginTop: 5,
  },
  profileImage: {
    width: 60,
    height: 60,
    borderRadius: 30,
  },
  childContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: 5,
    borderRadius: 20,
    marginBottom: 5,
  },
  greetingText: {
    fontSize: 20,
    color: '#333',
    marginTop: 5,
    marginHorizontal: screenWidth * 0.025,
  },
  profileContainerContent: {
    alignItems: 'center',
  },
  profileImageWrapper: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    marginHorizontal: 15,
  },
  profileName: {
    fontSize: 14,
    marginTop: 5,
    color: '#333',
  },
  addChildButton: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
  },
  addChildText: {
    marginTop: 5,
    fontSize: 14,
    color: '#4CAF50',
  },
  swipeContainer: {
    width: '100%',
    height: Platform.OS === 'web' ? 200 : 200,
    marginTop: 20,
  },
  functionContainer: {
    width: screenWidth * 0.95, // Make the container 95% of the screen width
    justifyContent: 'center',
    alignItems: 'center',
    padding: 20,
    borderRadius: 10,
    marginHorizontal: screenWidth * 0.025, // Adds 2.5% margin on each side for slight spacing
  },
  functionText: {
    marginTop: 10,
    fontSize: 20,
    fontWeight: 'bold',
    textAlign: 'center',
  },
});

export default ChildDashboard;
